@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ruda:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik&display=swap");
*,
::after,
::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased !important;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

body,
div,
p,
h1,
h2,
h3,
h4,
h5,
ul,
li,
a,
form {
  margin: 0;
  padding: 0;
  border: 0;
  text-decoration: none;
  line-height: 1;
}

button,
input,
textarea {
  outline: none !important;
  -webkit-transition: all .125s;
  -o-transition: all .125s;
  transition: all .125s;
  font-family: inherit;
}

input[type=text]:focus,
input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus,
input:active {
  outline: none !important;
}

::-webkit-input-placeholder {
  color: #222;
}

::-moz-placeholder {
  color: #222;
}

/* Firefox 19+ */
:-moz-placeholder {
  color: #222;
}

/* Firefox 18- */
:-ms-input-placeholder {
  color: #222;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

svg {
  -ms-flex-negative: 0;
  flex-shrink: 0;
}

svg,
path {
  -webkit-transition: all .125s;
  -o-transition: all .125s;
  transition: all .125s;
}

a {
  color: inherit;
  text-decoration: none;
  -webkit-transition: 0.125s;
  -o-transition: 0.125s;
  transition: 0.125s;
}

a:hover {
  text-decoration: none;
}

a:focus {
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

i {
  line-height: 100%;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  box-sizing: border-box;
  transition: 0.125s;
  background-color: inherit;
}

.swiper-button-prev,
.swiper-button-next {
  z-index: 2;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.display1,
.display2,
.display3,
.display4,
.display5 {
  font-family: "Ruda", sans-serif;
  font-weight: 500;
  line-height: 120%;
}

.display1 {
  font-size: 56px;
}

.display2 {
  font-size: 36px;
}

.display3 {
  font-size: 30px;
}

.display4 {
  font-size: 24px;
  line-height: 130%;
}

.display5 {
  font-size: 20px;
}

.title,
.title2 {
  font-family: "Ruda", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #98A2B3;
}

.title2 {
  font-size: 11px;
}

.body-text,
.body-text2,
.body-text3 {
  font-size: 16px;
  line-height: 140%;
}

.body-text p,
.body-text2 p,
.body-text3 p {
  line-height: inherit;
}

.body-text p:not(:last-child),
.body-text2 p:not(:last-child),
.body-text3 p:not(:last-child) {
  margin-bottom: 13px;
}

.body-text2 {
  font-size: 14px;
}

.body-text3 {
  font-size: 12px;
}

.text-link {
  font-size: 14px;
  line-height: 140%;
  color: #2B809C;
}

.text-link:hover {
  color: #364452;
}

.mlist {
  padding-left: 12px;
}

.mlist li {
  padding-left: 16px;
  line-height: inherit;
  position: relative;
}

.mlist li:not(:last-child) {
  margin-bottom: 2px;
}

.mlist li::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #344054;
}

.mbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all ease-out .225s;
  padding: 0;
}

.mbtn:focus {
  box-shadow: none;
}

.mbtn-white {
  background: #FCFCFD;
  color: #344054;
}

.mbtn-white:hover {
  color: #FCFCFD;
  background: #344054;
}

.mbtn-white-outline {
  border: 1px solid #FCFCFD;
  color: #FCFCFD;
}

.mbtn-white-outline:hover {
  background-color: #FCFCFD;
  color: #225B7B;
}

.mbtn-black-outline {
  border: 1px solid #344054;
  color: #344054;
}

.mbtn-black-outline:hover {
  background-color: #344054;
  color: #fff;
}

.mbtn-blue {
  background: #2C76A0;
  color: #fff;
}

.mbtn-blue:hover {
  background: #225B7B;
  color: #fff;
}

.swiper-button-next,
.swiper-button-prev {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.more-btn {
  margin: 60px auto 0;
  width: 380px;
  height: 52px;
  max-width: 100%;
  font-size: 14px;
  line-height: 140%;
}

.page__wrap {
  display: flex;
}

.page__wrap--gap100 .page__left {
  max-width: calc(100% - 440px);
}

.page__wrap--gap100 .page__right {
  margin-left: 100px;
}

.page__left {
  flex-grow: 1;
  max-width: calc(100% - 400px);
}

.page__right {
  width: 340px;
  flex-shrink: 0;
  margin-left: 60px;
}

.page-top {
  color: #344054;
  padding: 132px 0 72px;
}

.page-top--small {
  padding-bottom: 38px;
}

.page-top__items {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 20px;
}

.page-top__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 49px;
}

.page-top__subtitle {
  font-weight: 600;
  font-size: 11px;
  line-height: 140%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 4px;
}

.page-top__num {
  font-size: 16px;
  line-height: 140%;
}

.tags {
  width: calc(100% + 12px);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: -12px;
}

.tag {
  padding: 0 18px;
  height: 40px;
  white-space: nowrap;
  border-radius: 20px;
  background: #F2F4F7;
  border: 1px solid #98A2B3;
  font-size: 14px;
  color: #667085;
  display: flex;
  align-items: center;
  margin: 12px 12px 0 0;
}

.tag:hover {
  color: #F9FAFB;
  background-color: #98A2B3;
}

.tag:last-child {
  margin-right: 0;
}

.nice-select.my-select {
  color: inherit;
  float: unset;
  height: 52px;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 35px 0 20px;
  font-size: 14px;
  line-height: 140%;
  border-radius: 0;
  border: none;
  background: #FCFCFD;
}

.nice-select.my-select:before {
  content: 'â—„';
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(3px, -40%) rotate(-90deg);
  font-size: 16px;
  display: flex;
  align-items: center;
}

.nice-select.my-select:after {
  display: none;
}

.nice-select.my-select .list {
  padding: 20px 0;
  border-radius: 0;
  border: none;
  background: #FCFCFD;
  box-shadow: 0px 20px 24px -4px rgba(16, 24, 40, 0.1), 0px 8px 8px -4px rgba(16, 24, 40, 0.04);
}

.nice-select.my-select .option {
  border: none;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
}

.nice-select.my-select .option.selected {
  background-color: #F2F4F7 !important;
  font-weight: 400;
}

.fg {
  position: relative;
  margin: 0 0 20px;
}

.fg label {
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  display: block;
  color: #475467;
  margin-bottom: 8px;
}

.fg textarea,
.fg input {
  background: #FFFFFF;
  border: 1px solid #E4E7EC;
  border-radius: 3px;
  padding: 0 10px;
  height: 52px;
  width: 100%;
  font-size: 14px;
  line-height: 140%;
  color: #364452;
}

.fg textarea::placeholder,
.fg input::placeholder {
  color: #98A2B3;
}

.fg textarea:focus,
.fg input:focus {
  border-color: #364452;
}

.fg textarea {
  display: block;
  height: 100px;
  resize: none;
  padding-top: 10px;
  padding-bottom: 10px;
}

.check {
  position: relative;
}

.check input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 2;
  cursor: pointer;
}

.check input:checked + label::before {
  border: 3px solid #fff;
}

.check input:disabled + label {
  color: #AAAAAA;
  pointer-events: none;
}

.check label {
  padding-left: 40px;
  position: relative;
  min-height: 20px;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
}

.check label:before {
  content: '';
  position: absolute;
  z-index: 1;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.01);
  border: 1px solid rgba(255, 255, 255, 0.45);
  width: 20px;
  height: 20px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: all .100s;
}

.breadcrumbs {
  color: #344054;
  margin-bottom: 20px;
}

.breadcrumbs__item {
  font-weight: 600;
  font-size: 11px;
  line-height: 140%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  display: inline;
}

.breadcrumbs__item:not(:first-child):before {
  content: '>';
  margin: 0 8px 0 5px;
}

.project-card {
  display: block;
  background: #F9FAFB;
}

.project-card__img {
  height: 400px;
  width: 100%;
  overflow: hidden;
}

.project-card__img:hover img {
  transform: scale(1.1);
}

.project-card__img img {
  transition: transform 1.3s;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.project-card__content {
  padding: 32px 28px;
}

.project-card__info {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.project-card__info-item {
  margin: 10px 16px 0 0;
  font-size: 14px;
  line-height: 140%;
}

.blog-card {
  background: #FCFCFD;
  box-shadow: 0px 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06);
}

.blog-card__title:hover {
  color: #2B809C;
}

.blog-card__img {
  height: 260px;
  overflow: hidden;
}

.blog-card__img:hover img {
  transform: scale(1.1);
}

.blog-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 1.3s;
}

.blog-card__content {
  padding: 28px 28px 41px;
}

.blog-card__date {
  color: #9DA5AE;
  margin-top: 8px;
}

.blog-card__desc {
  color: #737E8A;
  margin: 16px 0;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.news-card {
  background: #FCFCFD;
  box-shadow: 0px 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06);
  display: flex;
  align-items: stretch;
}

.news-card__title:hover {
  color: #2B809C;
}

.news-card__img {
  width: 240px;
  overflow: hidden;
  flex-shrink: 0;
}

.news-card__img:hover img {
  transform: scale(1.1);
}

.news-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 1.3s;
}

.news-card__content {
  padding: 40px;
}

.news-card__info {
  display: flex;
  align-items: center;
  margin-top: 4px;
  color: #737E8A;
}

.news-card__info-title {
  margin-left: 8px;
  font-size: 12px;
  line-height: 140%;
}

.news-card__desc {
  margin: 12px 0 20px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.qa-card {
  background: #F2F4F7;
}

.qa-card--active .qa-card__btn {
  transform: rotate(-45deg);
}

.qa-card:not(:last-child) {
  margin-bottom: 4px;
}

.qa-card__show {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  cursor: pointer;
}

.qa-card__title {
  font-size: 18px;
  line-height: 140%;
}

.qa-card__btn svg {
  width: 28px;
  height: 28px;
  fill: #344054;
}

.qa-card__hidden {
  padding: 20px 80px 26px 46px;
  color: #667085;
  display: none;
}

.modal-dialog {
  background: #F2F4F7;
  padding: 70px 68px 100px;
  pointer-events: unset;
  width: 800px;
  max-width: 100%;
}

.modal__close {
  top: 20px;
  right: 20px;
  position: absolute;
}

.modal__close svg {
  width: 32px;
  height: 32px;
  fill: #667085;
}

.modal__close:hover svg {
  fill: #364452;
}

.modal__title {
  color: #344054;
}

.modal__form {
  margin-top: 32px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px 24px;
}

.modal__form .fg {
  margin: 0;
}

.modal__form .fg--text {
  display: flex;
  flex-direction: column;
  grid-row: 1/4;
  grid-column: 2/3;
}

.modal__form .fg--text textarea {
  flex-grow: 1;
}

.modal__hint {
  margin-top: 24px;
  color: #667085;
}

.modal__hint a {
  color: #344054;
}

.modal__hint a:hover {
  text-decoration: underline;
}

.modal__btn {
  margin-top: 24px;
  width: 199px;
  height: 54px;
  font-size: 16px;
}

.modal__btn svg {
  margin-right: 8px;
  fill: #fff;
  width: 22px;
  height: 22px;
}

.header {
  padding: 22px 0;
  position: relative;
  z-index: 3;
}

.header--light .header__logo svg {
  fill: #fff;
}

.header--light .header__burger span {
  background-color: #fff;
}

.header--light .header__btn svg {
  fill: #fff;
}

.header--light .header__phone {
  stroke: #fff;
}

.header--active .header__logo svg {
  fill: #fff;
}

.header--active .header__burger span {
  background-color: #fff;
}

.header--active .header__burger span:nth-child(1) {
  transform: rotate(-45deg) translate(-8px, 5px);
}

.header--active .header__burger span:nth-child(2) {
  transform: scale(0);
}

.header--active .header__burger span:nth-child(3) {
  transform: rotate(45deg) translate(-8px, -5px);
}

.header--active .menu {
  opacity: 1;
  pointer-events: unset;
}

.header .container {
  max-width: 100%;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  width: 170px;
  height: 28px;
  position: relative;
  z-index: 15;
}

.header__logo svg {
  width: 100%;
  height: 100%;
  fill: #364452;
}

.header__buttons {
  display: flex;
  align-items: center;
}

.header__btn {
  margin-left: 36px;
}

.header__btn svg {
  width: 24px;
  height: 24px;
  fill: #364452;
}

.header__phone {
  stroke: #364452;
}

.header__burger {
  width: 24px;
  height: 20px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 15;
}

.header__burger span {
  display: block;
  width: 100%;
  height: 1.31px;
  background: #364452;
  left: 0;
  transition: all .3s;
}

.header__burger span:nth-child(1) {
  position: absolute;
  top: 0;
}

.header__burger span:nth-child(3) {
  position: absolute;
  bottom: 0;
}

.menu {
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #225B7B;
  z-index: 12;
  padding: 100px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu__grid {
  max-width: 930px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.menu__title, .menu__link, .menu__link2 {
  font-size: 18px;
  line-height: 140%;
  color: #D0D5DD;
}

.menu__title {
  font-weight: 700;
}

.menu__links {
  display: grid;
  grid-template-columns: 100%;
  gap: 16px;
  margin: 24px 0;
}

.menu__link:hover, .menu__link2:hover {
  color: #fff;
}

.footer {
  padding: 84px 0 105px;
  background: #1B4760;
  color: #D0D5DD;
}

.footer__logo svg {
  fill: #D0D5DD;
  width: 216px;
  height: 48px;
}

.footer__wrap {
  display: flex;
  margin-top: 41px;
  justify-content: space-between;
}

.footer__left {
  max-width: 200px;
}

.footer__right {
  flex-grow: 1;
  max-width: 880px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.footer__address {
  font-size: 10px;
  line-height: 140%;
  font-family: "Rubik", sans-serif;
  margin-bottom: 20px;
}

.footer__address span {
  display: block;
}

.footer__socials {
  display: flex;
  align-items: center;
}

.footer__socials-item {
  margin-right: 10px;
}

.footer__socials-item svg {
  width: 32px;
  height: 32px;
  fill: rgba(255, 255, 255, 0.6);
}

.footer__socials-item:hover svg {
  fill: white;
}

.footer__links {
  display: flex;
  flex-direction: column;
}

.footer__link {
  font-size: 16px;
  line-height: 140%;
}

.footer__link:hover {
  color: #fff;
}

.footer__link:not(:last-child) {
  margin-bottom: 10px;
}

.sidebar {
  position: sticky;
  top: 20px;
}

.sidebar__block {
  background: #FCFCFD;
  margin-bottom: 2px;
  padding: 40px 20px;
}

.sidebar .tags {
  margin-top: 30px;
}

.sidebar-nav__links {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
}

.sidebar-nav__link {
  font-size: 16px;
  line-height: 140%;
}

.sidebar-nav__link:not(:last-child) {
  margin-bottom: 24px;
}

.sidebar-nav__link:hover {
  color: #2B809C;
}

.sidebar-nav__link--active {
  font-weight: 700;
}

.sidebar-blog__items {
  margin-top: 20px;
}

.sidebar-blog__item {
  padding: 12px 0;
  display: flex;
  align-items: center;
}

.sidebar-blog__img {
  width: 60px;
  height: 60px;
  flex-shrink: 0;
  margin-right: 16px;
}

.sidebar-blog__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.sidebar-blog__date {
  margin-top: 8px;
  color: #737E8A;
}

html {
  width: 100%;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #364452;
  background: #F2F4F7;
}

body.fixed-body {
  overflow-y: hidden;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 30px;
}

@media screen and (max-width: 992px) {
  .container {
    padding: 0 20px;
  }
}

.container--small {
  max-width: 960px;
}

.main {
  margin-top: -72px;
  color: #FCFCFD;
  position: relative;
  overflow: hidden;
}

.main .swiper-slide {
  padding: 300px 300px 300px 80px;
  position: relative;
  height: 100vh;
  max-height: 960px;
  background-size: cover;
}

.main .swiper-slide::before {
  content: '';
  position: absolute;
